import React, { FC } from "react";
import clsx from "clsx";

import { Coins, Dices, Lights } from "./components";

import button from "./assets/button.svg";
import name from "./assets/name.svg";
import css from "./style.module.scss";

interface Props {
  play: boolean;
}

const HashDiceBanner: FC<Props> = ({ play }) => {
  return (
    <div className={clsx(css.container, !play && css.disable)}>
      <Lights />
      <Coins />
      <Dices />

      <img src={name} alt="name" className={css.name} />
      <span className={css.buttonWrapper}>
        <div className={css.shadowAnimate} />
        <img src={button} alt="button" className={css.button} />
      </span>
    </div>
  );
};

export default HashDiceBanner;
