import React from "react";
import { repeat } from "utils";
import css from "./style.module.scss";
import Line from "./line_.webp";

export const Lights = () => {
  return (
    <div className={css.root}>
      {repeat(16, (i, n) => {
        const angle = 360 / n;
        const rotate = angle * i;

        const lineId = `line_${i}_${rotate}`;

        return (
          <img
            src={Line}
            alt={lineId}
            key={lineId}
            className={css.line}
            style={{
              transform: `rotate(${rotate}deg)`,
            }}
          />
        );
      })}
      <div className={css.circle} />
    </div>
  );
};
