import clsx from "clsx";
import React from "react";

import b_1 from "../../assets/coins/b_1.webp";
import b_2 from "../../assets/coins/b_2.webp";
import b_3 from "../../assets/coins/b_3.webp";

import _1 from "../../assets/coins/_1.webp";
import _2 from "../../assets/coins/_2.webp";
import _3 from "../../assets/coins/_3.webp";
import _4 from "../../assets/coins/_4.webp";
import _5 from "../../assets/coins/_5.webp";
import _6 from "../../assets/coins/_6.webp";
import _7 from "../../assets/coins/_7.webp";

import css from "./style.module.scss";

const bigCoins = [b_1, b_2, b_3];
const smallCoins = [_1, _2, _3, _4, _5, _6, _7];

export const Coins = () => {
  return (
    <>
      {bigCoins.map((src, i) => (
        <img
          src={src}
          alt={`bigCoin_${i}`}
          className={clsx(css.coin, css[`coin__${i + 1}`])}
        />
      ))}

      {smallCoins.map((src, i) => (
        <img
          src={src}
          alt={`smallCoin_${i}`}
          className={clsx(css.smallCoin, css[`smallCoin__${i + 1}`])}
        />
      ))}
    </>
  );
};
