import clsx from "clsx";
import React, { FC } from "react";

import _01 from "../../assets/dices/_1.webp";
import _02 from "../../assets/dices/_2.webp";
import _03 from "../../assets/dices/_3.webp";
import _04 from "../../assets/dices/_4.webp";
import _05 from "../../assets/dices/_5.webp";
import _06 from "../../assets/dices/_6.webp";

import s_2 from "../../assets/shadows/_2.webp";
import s_3 from "../../assets/shadows/_3.webp";
import s_4 from "../../assets/shadows/_4.webp";
import s_6 from "../../assets/shadows/_6.webp";

import css from "./style.module.scss";

const list = [
  {
    img: _01,
  },
  {
    img: _02,
    shadow: s_2,
  },
  {
    img: _03,
    shadow: s_3,
  },
  {
    img: _04,
    shadow: s_4,
  },
  {
    img: _05,
  },
  {
    img: _06,
    shadow: s_6,
  },
];

export const Dices = () => {
  return (
    <>
      {list.map(({ img, shadow }, i) => {
        if (shadow) {
          return (
            <div
              key={`_${i}_${img}_${shadow}`}
              className={clsx(
                css.withShadowWrapper,
                css[`withShadowWrapper__${i + 1}`],
              )}>
              <img src={shadow} alt="shadow" />
              <img src={img} alt="dice" />
            </div>
          );
        }

        return (
          <img
            key={`_${i}_${img}`}
            src={img}
            className={clsx(css.dice, css[`dice__${i + 1}`])}
            alt={`dice_${i}`}
          />
        );
      })}
    </>
  );
};
